<template>
	<div id="operate-stock">
		<!--顶部菜单-->
		<nav-bar class="operate-stock-nav-bar">
			<div slot="left" class="icon-left" @click="backClick">
				<img src="~assets/images/common/prev2.svg">
			</div>
			<div slot="center">
				{{title}}
			</div>
			<div slot="right" @click="listClick">
				列表
			</div>
		</nav-bar>
		<!--滚动--->
		<scroll class="addorder-scroller"
				ref="scroller"
				:probe-type=3
				@pullingup="loadMove"
		>
		<add-order-content :base-info="baseInfo" ref="addcontent"/>
		</scroll>
		<add-tabbar @successClick="successClick"/>
		
	</div>
</template>

<script>

import NavBar from 'components/common/navbar/NavBar'
import Scroll from 'components/common/scroll/Scroll'

import AddOrderContent from './childComps/AddOrderContent'
import AddTabbar from './childComps/AddTabbar'

import {SeedAddOperateStock} from 'network/order'



export default {
  name: 'OperateStock',
	components: {
		NavBar,
		Scroll,
		AddOrderContent,
		AddTabbar
	},
	data(){
		return{
			title:'新增订单',
			pathName:'/salesslip',
			//后台需要的数据 状态,地址,订单编号,操作人,订单类型,是否待审核,已付金额，快递id，客户id 快递单号 
			btnName:'保存',
			baseInfo:{
				"productid":0,						//商品
				"expressid":1,						//快递id
				"expressno":'',						//快递单号
				"supplierid":'',					//客户id
				"amount":0,							//总金额
				"handlerid":1,						//操作人id
				"backmark":"备注",					//备注
				"categoryid":1,						//类型
				"documentstype":0,
				"status":0,							//状态
				
				"addressid":'',						//地址-选择客户的时候带有地址
				"ordercode":'',						//订单编号-
				"handler":'小锐',					//操作人
				"documentid":3,						//订单类型
				"express":"顺丰快递",				//快递id
				"contact":'',						//联系人
				"telephone":'',						//联系电话
				"unittext":'',						//公司名称(客户名称)
				
				//orderdetail-订单里的货品-vuex里面拿
				orderproduct:[
					{
						userid:0,					//操作人
						productid:0,				//商品id
						executed:'',				//执行状况
						price:0,					//价格
						productnum:0,				//商品数量
					}
				],
			},
		}
	},
	methods:{
		//返回按钮
		backClick(){
			//this.$router.replace("/customersdetail").catch(res=>{res})
			this.$router.go(-1)
		},
		//加载更多
		loadMove(){
			console.log("加载更多")
		},
		//网络请求
		SeedAddOperateStock(obj){
			SeedAddOperateStock(obj).then(res=>{
				//这里是后台传出来的
				console.log(res)
				//订单操作完毕需要-去库存数量
				//成功后清空页面跳转
				//新增一个出入库单的表
			})	
		},
		//保存按钮
		successClick(){
			console.log("保存按钮")
			//console.log(this.baseInfo)
			this.SeedAddOperateStock(this.baseInfo)
		},
		//列表
		listClick(){
			this.$router.push("/historyreceipts").catch(err=>{err})
		}
		
	},
	activated(){
		//需要清空一下选中的类名
		this.baseInfo.supplierid=this.$store.state.customerInfo.id				//id
		this.baseInfo.contact=this.$store.state.customerInfo.contact			//联系人
		this.baseInfo.telephone=this.$store.state.customerInfo.phone			//联系电话
		this.baseInfo.unittext=this.$store.state.customerInfo.unittext			//客户名称
		this.baseInfo.addressid=this.$store.state.customerInfo.addressid		//客户地址
		this.baseInfo.orderproduct=this.$store.state.orderProductList			//选取的商品列表
		
		
		//判断是什么类型的订单
		if(this.$route.params.type=="outstock"){
			this.title="出库单";
			this.baseInfo.status=1
			this.$refs.addcontent.iCurrentIndex=1
			this.pathName="/salesslip";
		}else if(this.$route.params.type=="instock"){
			this.title="入库单"
			this.baseInfo.status=0
			this.$refs.addcontent.iCurrentIndex=0
			this.pathName="/orderhistory";
		}
	}
}
</script >

